import React from "react";
import "./Routing.css";
import { BrowserRouter, Routes, Route, Link } from "react-router-dom";
import Home from "../components/Home/Home";
import About from "../components/About/About";
import Event from "../components/Event/Event";
import Contact from "../components/Contact/Contact";
class Routing extends React.Component {
  constructor() {
    super();
    this.state = {
      isNavExpanded: false,
    };
  }
  handleClickNav = () => {
    this.setState((prevState) => ({
      isNavExpanded: !prevState.isNavExpanded,
    }));
  };
  render() {
    const test = this.state.isNavExpanded;
    return (
      <BrowserRouter>
        <nav className="navigation">
          <img src="https://i.ibb.co/n3CgzqQ/Pandharpur.png" alt="" />
          <a href="/" className="brand-name">
            ISKCON Pandharpur
          </a>

          <button className="hamburger" onClick={this.handleClickNav}>
            {/* icon from Heroicons.com */}
            <svg
              xmlns="http://www.w3.org/2000/svg"
              className="h-5 w-5"
              viewBox="0 0 20 20"
              fill="white"
            >
              <path
                fillRule="evenodd"
                d="M3 5a1 1 0 011-1h12a1 1 0 110 2H4a1 1 0 01-1-1zM3 10a1 1 0 011-1h12a1 1 0 110 2H4a1 1 0 01-1-1zM9 15a1 1 0 011-1h6a1 1 0 110 2h-6a1 1 0 01-1-1z"
                clipRule="evenodd"
              />
            </svg>
          </button>
          <div
            className={test ? "navigation-menu expanded" : "navigation-menu"}
          >
            <ul>

              <li>
                <Link to="/home" className="link" onClick={this.handleClickNav}>
                  Home
                </Link>
              </li>
              <li>
                <Link
                  to="/about"
                  className="link"
                  onClick={this.handleClickNav}
                >
                  About
                </Link>
              </li>
              <li>
                <Link
                  to="/contact"
                  className="link"
                  onClick={this.handleClickNav}
                >
                  Contact
                </Link>
              </li>
            </ul>
          </div>
            <button className="donate-btn-top">Donate</button>
        </nav>
        <Routes>
          <Route path="/" element={<Home />} />
          <Route path="/about" element={<About />} />
          <Route path="/event" element={<Event />} />
          <Route path="/contact" element={<Contact />} />
        </Routes>
      </BrowserRouter>
    );
  }
}

export default Routing;
